import React, { useEffect } from 'react';
import '../App.css';
import CoinExpress from '../components/CoinExpress';
import CoinPersonas from '../components/CoinPersonas';

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..

const Acceso = () => {
  useEffect(() => {
  AOS.init({ duration: 2000 });
  }, []);

  return (
    <>      
      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='header-box'>
          <div className="item-box">
            <h1 className='title-item'> Acceso Clientes</h1>
          </div>
          <div className="item-box">
            <h4 className='subtitle-item'> </h4>
          </div> 
        </div>
      </div>

      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className="content-box">
          <CoinExpress />  
          <CoinPersonas />   
        </div>
      </div>
            
    </>
  );
}

export default Acceso;