import React from 'react'
import Slider from 'infinite-react-carousel'

import './Slider.css'

const settings =  {
      autoplay: true,
	  autoplaySpeed: 2000
    };

const Carousel = ({ images }) => {
	return (
		<section className='slider'>
			
			<Slider { ...settings } className='slider__content'>
				{images.map((image) => (
					<div key={image.id} className='slider__content--item'>
						<img src={image.image} alt={image.title} />
						<p className='slider-description'>{image.title}</p>
						<p className='slider-description1'>{image.subtitle}</p>
						<p className='slider-description2'>{image.subtitle2}</p>
											

						
					</div>
				))}
			</Slider>
		</section>
	)
}

export default Carousel
