import React, { useEffect } from 'react';
import './../App.css';
import './../components/Navbar.css'
import './Pages.css';

import LogosClientes from './../components/LogosClientes';

import Carrousel from './../components/Carrousel';

import LogosClientesmobile from './../components/LogosClientesmobile';

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..

const Clientes = () => {
  useEffect(() => {
  AOS.init({ duration: 2000 });
  }, []);


  return (
    <> 
      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='header-box'>
        <div className="item-box5">
        <h1 className='title-item'> Algunos de nuestros clientes</h1>
        </div>        
        <div className="item-box">
        <h4 className='subtitle-item'> </h4>
        </div>  

      </div>
      </div>

      <div data-aos="fade-up" className='wrapper-boxes desktop'>
        <LogosClientes />
      </div>

      <div data-aos="fade-up" className='wrapper-boxes mobile'>
        <LogosClientesmobile />
      </div>

      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='header-box'>
        <div className="item-box5">
        <h1 className='title-item'>Algunos Testimonios</h1>
        </div> 
      </div>
      </div>
      
      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='box-carrousel'>
          <Carrousel />
        </div>
      </div>
      
    </>
  );
}

export default Clientes;