import React, { useEffect } from 'react';
import './../App.css';
import './../components/Navbar.css'
import './Pages.css';

import Formcontact from './../components/Formcontact';
import './../components/Formulario/Form.css';

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..

const Contacto = () => {
  useEffect(() => {
  AOS.init({ duration: 2000 });
  }, []);
  
  return (
    <>      
      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='header-box'>
          <div className="item-box">
            <h1 className='title-item'> Contacto</h1>
          </div>
          <div className="item-box">
            <h4 className='subtitle-item'> </h4>
          </div>
        </div>
      </div>

      <div data-aos="fade-up" className='wrapper-boxes'>
        <div className='form-container-gateway'>        
          <div className='form-box-left-con'>          
            <h1 className='super-title'> Para Adherirse al servicio o para mayor información, completá el formulario.</h1>
            <h4 className='subtitle-item'> </h4>
          </div>
          <Formcontact />        
        </div>
      </div>

      

       


            
            
    </>
  );
}

export default Contacto;