import React from 'react';
import './GatewayElements.css'
import imgs1 from './../../images/pay-1.png';
import { Link } from 'react-router-dom';

import { InfoContainer, InfoWrapper, InfoRow, Column1, TextWrapper, FirstLine, TopLine, Heading, Subtitle, ImgWrap, Img } from './GatewayElements';
    
const Gateway = ({lightBg, id, imgStart, secondBlock, firstBlock, topLine, topLine2, lightText, headline, headline2, darkText, description, description2, alt }) => {

const mystyle = {
        display: "none",
      };

    return (
        <>
            <InfoContainer lightBg={lightBg} id={id}>
                <InfoWrapper>
                    <div className="servicesd-box" style={mystyle}>
                    <FirstLine>{firstBlock}<br/><span className="SeconfBlock">{secondBlock}</span></FirstLine>                                        
                    </div>
                    <div><p className='separator'></p></div>
                    <InfoRow imgStart={imgStart}>                      

                        <Column1>
                            <ImgWrap>
                                <Img src={imgs1} alt={alt} />                                
                            </ImgWrap>
                            <TextWrapper>
                                <TopLine>{topLine}</TopLine>
                                <Heading lightText={lightText}>{headline}</Heading>
                                <Subtitle darkText={darkText}>{description}</Subtitle>
                                
                                <Link to='/gateway' className='btn-wrap' >Comenzar</Link>       

                                
                            </TextWrapper>                                                    
                        </Column1>

    
                    </InfoRow>
                </InfoWrapper>
            </InfoContainer>
        
        
        </>   
    );
};

export default Gateway;