import React, { useEffect } from 'react';
import '../App.css';
import Gateway from './../components/Gateway';
import { homeObjOne } from './../components/Gateway/Data';
import SlideShow from './../components/SlideShow';
import Services from './../components/Services';
import PayServices from './../components/PayServices';

import Suscripcion from './../components/Suscripcion';

import AOS from 'aos';
import 'aos/dist/aos.css'; // You can also use <link> for styles
// ..

const Home = () => {
  useEffect(() => {
  AOS.init({ duration: 2000 });
  }, []);

  return (

    <>      
      <div data-aos="fade-up" className='slideshow-box'>
        <SlideShow />
      </div>

      <div data-aos="fade-up" className='header-box'>
        <div className="item-box5">
        <h1 className='title-item'>Soluciones de pagos Online</h1>
        </div>
      </div>
      <div data-aos="fade-up"><Gateway {...homeObjOne} /></div>
      <div data-aos="fade-up"><Services /></div>
      <div data-aos="fade-up"><PayServices /></div>
      <div data-aos="fade-up"><Suscripcion /></div>
    </>
  );
}

export default Home;